:root {
    --color-bg: #1F2833;
    --color-bg-variant: #0B0C10;
    --color-primary: #66FCF1;
    --color-primary-variant: #45A29E;
    --color-white: #fff;
    --color-light: #C5C6C7;
    --transiton: all 400ms ease;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --swiper-theme-color: var(--color-primary-variant) !important;
}


html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/bg-texture.png); */
}

/* ===General Styles==== */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}





section > h2,section>h5 {
    text-align: center;
    color: var(--color-light)
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;

}


.section-header {
    display: flex;
    justify-content: center;
    padding-bottom: 42px;
}

.section-header::after {
    content: "";
    display: block;
    margin-top: 39px;
    height: 1px;
    width: 300px;
    margin-left: 20px;;
    background-color: var(--color-primary);
    }

.section-title {
    text-align: center;
    color: var(--color-primary);
    font-size: 46px;
    font-weight: bold;
    margin-bottom: 3rem;
    font-weight: bold;
}


.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

emp {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ====== MEDIA QUERIES (MEDIUM DEVICES) ======== */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md)
    }
    section {
        margin-top: 6rem;
    }
    .section-header::after {
        display: none;
    }
}

/* ====== MEDIA QUERIES (SMALL DEVICES) ======== */
@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-sm)
    }
    section > h2{
        margin-bottom: 2rem;
    }

    .section-header::after {
        display: none;
    }
    .section-title {
        font-size: 23px;
    }
}